import React,{useState, useEffect} from 'react';
import {Link} from 'react-router-dom';
import{MdFingerprint} from 'react-icons/md';
import { FaBars, FaTimes} from 'react-icons/fa';
import {Button} from './Button';
import './Navbar.css';

function Navbar(){

		const[click, setClick] = useState(false);
		const[button, setButton] = useState(true);

		const handleClick = () => setClick(!click);
		const closeMobileMenu = () => setClick(false);

		const showButton = () =>{
			if(window.innerWidth <= 960){
				setButton(false);
			}else{
				setButton(true);
			}

		};

		useEffect(() => {

			showButton();
		}, []);

		window.addEventListener('resize', showButton);

		return(
			<>
				<div className='navbar'>
					<div className='navbar-container container'>
						<Link className='navbar-logo' onClick={closeMobileMenu}>
							<MdFingerprint className='navbar-icon'/>
							Glade
						</Link>
						
						<div className='menu-icon' onClick={handleClick}>
						{click ? <FaTimes /> : <FaBars/>}
						</div>
						<ul className={click ? 'nav-menu active' : 'nav-menu'}>
							<li className='nav-item'>
								<Link to='/' className='nav-links' onClick={closeMobileMenu}>
									Create New Card
								</Link>
							</li>
							<li className='nav-item'>
								<Link to='/features' className='nav-links' onClick={closeMobileMenu}>
									Features
								</Link>
							</li>
							<li className='nav-item'>
								<Link to='/transactions' className='nav-links' onClick={closeMobileMenu}>
									My Transactions
								</Link>
							</li>
							<li className='nav-btn'>
								{button ? (
									<Link to='/login' className='btn-link'>
										<Button buttonStyle='btn--outline'> Login </Button>
									</Link>
									): (
									<Link to='/login' className='btn-link' onClick={closeMobileMenu}>
										<Button buttonStyle='btn--outline' buttonSize='btn--mobile'>Login </Button>
									</Link>
									)}
							</li>
						</ul>
					</div>
				</div>
			</>
			);
}

export default Navbar;